import { formLoaderActions } from "./../redux/slices/formLoader/formLoaderSlice";
import { faqActions } from "../redux/slices/faq/faqSlice";
import http from "./http.service";
import { AppDispatch } from "redux/store";
import Promisable from "./promisable.service";
import { modalActions } from "redux/slices/modal";
import { NavigateFunction } from "react-router-dom";

const url = `/FAQ`;

const FaqService = {
  getFaqbyID: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(formLoaderActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.get(`${url}/${id}`)
    );

    if (success) {
      const { faq } = success.data.data;
      dispatch?.(faqActions.setFaq(faq));
    }

    dispatch?.(formLoaderActions.setLoading(false));

    return [success, error];
  },
  getFaqs: async (data: any, dispatch?: AppDispatch) => {
    dispatch?.(faqActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}/filter`, data)
    );

    if (success) {
      const { FAQs } = success.data.data;
      dispatch?.(faqActions.setFaqs(FAQs));
    }

    dispatch?.(faqActions.setLoading(false));

    return [success, error];
  },

  createFaq: async (
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(modalActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.post(`${url}`, data)
    );

    if (success) {
      navigate?.("/faq-items");
    }

    dispatch?.(modalActions.setLoading(false));

    return [success, error];
  },

  deleteFaq: async (id: any, dispatch?: AppDispatch) => {
    dispatch?.(modalActions.setLoading(true));

    http.setJWT();

    const [success, error]: any = await Promisable.asPromise(
      http.delete(`${url}/${id}`)
    );

    if (success) {
      dispatch?.(modalActions.closeModal());
      dispatch?.(faqActions.filterfaq(id));
    }

    dispatch?.(modalActions.setLoading(false));

    return [success, error];
  },

  updateFaq: async (
    id: string,
    data: any,
    dispatch?: AppDispatch,
    navigate?: NavigateFunction
  ) => {
    dispatch?.(formLoaderActions.setLoading(true));
    http.setJWT();
    const [success, error]: any = await Promisable.asPromise(
      http.put(`${url}/${id}`, data)
    );
    if (success) {
      const faq = success.data.data;
      dispatch?.(faqActions.updateFaq({ id, faq }));
      navigate?.("/faq-items");
    }
    dispatch?.(formLoaderActions.setLoading(false));
    return [success, error];
  },
};

export default FaqService;
