import React from "react";
import { fields } from ".";
import { useAppDispatch } from "redux/hooks";

import FiltersForm from "components/templates/FiltersForm";
import { faqActions } from "redux/slices/faq/faqSlice";
import { reset } from "redux-form";

export default function FaqItemsFilter() {
  const dispatch = useAppDispatch();
  const form = "FaqFiltersForm";

  const handleSubmit = (values: any) => {
    const data = { ...values };

    if (data.pageSize) data.pageSize = Number(data.pageSize);

    dispatch(faqActions.setFilter({ filters: data }));
  };

  return (
    <FiltersForm
      form={form}
      onSubmit={handleSubmit}
      myFields={fields}
      onClickReset={() => {
        dispatch(reset(form));
        dispatch(faqActions.setFilter({}));
      }}
    />
  );
}
