import React, { useEffect } from "react";

import { StyledTableCell, StyledTableRow } from "components/templates/Tables";
import {
  Table,
  TableRow,
  TableBody,
  TableHead,
  TableContainer,
} from "@mui/material";
import Stack from "@mui/material/Stack";

import { BsTrashFill } from "react-icons/bs";
import { AiOutlineEdit } from "react-icons/ai";

import TableLoadingWrapper from "components/templates/TableLoadingWrapper";
import Button from "components/atoms/Button";
import { useNavigate } from "react-router-dom";
import { MODAL } from "redux/slices/modal";
import { modalActions } from "redux/slices/modal";
import { useAppDispatch, useAppSelector } from "redux/hooks";
import FaqService from "services/faq.service";

const FaqItemsList = () => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const faqs = useAppSelector((state) => state.faq.faqs);
  const loading = useAppSelector((state) => state.faq.loading);
  const filter = useAppSelector((state) => state.faq.filter);
  const refreshLoader = useAppSelector((state) => state.faq.refreshLoader);

  useEffect(() => {
    FaqService.getFaqs(filter, dispatch);
  }, [filter, dispatch]);

  return (
    <div>
      <TableContainer>
        <Table
          aria-label="customized table"
          sx={{
            minWidth: "100%",
            borderSpacing: "0 10px",
            borderBottomWidth: "0px",
            borderCollapse: "separate",
          }}
        >
          <TableLoadingWrapper
            container
            coloumns={3}
            loading={loading}
            message="No faq's available"
            length={refreshLoader ? 0 : faqs?.length}
          >
            <TableHead>
              <TableRow>
                <StyledTableCell>Display Order</StyledTableCell>
                <StyledTableCell>Question</StyledTableCell>
                <StyledTableCell></StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {faqs.map((data: any, i: number) => (
                <StyledTableRow key={i}>
                  <StyledTableCell>{data.displayOrder}</StyledTableCell>
                  <StyledTableCell>{data.question}</StyledTableCell>
                  <StyledTableCell align="right">
                    <Stack direction="row" spacing={2} justifyContent="right">
                      <Button
                        // size="large"
                        variant="contained"
                        color="primary"
                        disableElevation
                        onClick={() =>
                          dispatch(
                            modalActions.openModal({
                              width: "500px",
                              type: MODAL.CONFIRMATION_FORM,
                              data: {
                                id: data._id,
                                type: MODAL.DELETE_FAQ,
                                heading: "Delete FAQ",
                                message:
                                  "Do you really want to delete this FAQ?",
                              },
                            })
                          )
                        }
                        sx={{ minWidth: "auto" }}
                      >
                        <BsTrashFill />
                      </Button>
                      <Button
                        // size="large"
                        variant="contained"
                        color="secondary"
                        disableElevation
                        onClick={() => navigate(`/update-faq/${data._id}`)}
                        sx={{ minWidth: "auto" }}
                      >
                        <AiOutlineEdit />
                      </Button>
                    </Stack>
                  </StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </TableLoadingWrapper>
        </Table>
      </TableContainer>
    </div>
  );
};

export default FaqItemsList;
