import { Container, Stack } from "@mui/material";
import Button from "components/atoms/Button";
import Banner from "components/templates/Banner";
import BasicCard from "components/templates/BasicCard";
import { useNavigate } from "react-router-dom";
import { useAppSelector } from "redux/hooks";
import FaqItemsFilter from "./FaqItemsFilters/FaqItemsFilter";
import FaqItemsList from "./FaqItemsLIst/FaqItemsList";



const FaqItems = () => {
    const faqs = useAppSelector((state) => state.faq.faqs);
    const navigate = useNavigate();
    return (
        <Container maxWidth="lg">
            <Banner heading={`Faq Item Management (${faqs.length})`}>
                <Stack direction="row" spacing={2}>
                    <Button
                        color="secondary"
                        variant="contained"
                        sx={{ borderRadius: "25px" }}
                    >
                        Export
                    </Button>
                    <Button
                        color="primary"
                        variant="contained"
                        sx={{ borderRadius: "25px" }}
                        onClick={() => navigate("/add-faqItems")}
                    >
                        + Add New
                    </Button>
                </Stack>
            </Banner>
            <BasicCard>
                <FaqItemsFilter />
            </BasicCard>
            <BasicCard>
                < FaqItemsList />
            </BasicCard>
        </Container>
    );
}

export default FaqItems